import { CChartDoughnut } from '@coreui/react-chartjs';
import React, { lazy } from 'react';
import ChartBarSimple from '../charts/ChartBarSimple.js';
import { userContext } from '../../context/UserContext';
import { url } from 'src/helpers/helpers';

const WidgetsDropdown = lazy(() => import('../widgets/WidgetsDropdown.js'))

const Dashboard = () => {
  const { user,setLoad } = React.useContext(userContext);
  const [pie_data,setTotalData] = React.useState([]);
  const [date_array,setDateArray] = React.useState([]);
  const [purchase_record,setPurchaseRecord] = React.useState([]);
  const [sales_record, setSalesRecord] = React.useState([]);
  const [expense_record, setExpenseRecord] = React.useState([]);
  
  React.useEffect(()=>{
    
    async function fetchPieData(){
      setLoad(true);
      const response = await fetch(url + 'get-total-profile-record', {
        method: 'GET',
        headers: {
            'Authorization': user?.token
        }
    })

    if (response.ok === true) {
        const data = await response.json();
        setLoad(false)
        if (data.status === 200) {
           let total_record=[];
           total_record.push(parseFloat(data.sales_record));
           total_record.push(parseFloat(data.purchase_record));
           total_record.push(parseFloat(data.expense_record));
           setTotalData(total_record);
        } else {
            alert(data.message);
        }
    }
    }
    fetchPieData();
    
    async function fetchData() {
      setLoad(true);
      const response = await fetch(url + 'fetchLastWeekSales', {
          method: 'GET',
          headers: {
              'Authorization': user?.token
          }
      })

      if (response.ok === true) {
          const data = await response.json();
          setLoad(false)
          if (data.status === 200) {
              setDateArray(data.date_array);
              setPurchaseRecord(data.purchase_record);
              setSalesRecord(data.sale_record);
              setExpenseRecord(data.expense_record);
          } else {
              alert(data.message);
          }
      }
  }

  fetchData();
  },[])
  return (
    <>
      <div className='row'>
        <div className='col-md-6'>
          <CChartDoughnut datasets={[
            {
              backgroundColor: [
                '#41B883',
                '#E46651',
                '#00D8FF',
              ],
              data: pie_data
            }
          ]}
            labels={['Sales', 'Purchase', 'Expense']}
            options={{
              tooltips: {
                enabled: true
              }
            }} />
        </div>

        <div className='col-md-6'>
        <WidgetsDropdown />
        </div>
      </div>
      <ChartBarSimple style={{ height: "400px", marginTop: 40 }} backgroundColor='#41B883' dataPoints={sales_record} date_array={date_array} label="Sales" />
      <ChartBarSimple style={{ height: "400px", marginTop: 40 }} backgroundColor='#E46651' dataPoints={purchase_record} date_array={date_array} label="Purchase" />
      <ChartBarSimple style={{ height: "400px", marginTop: 40 }} backgroundColor='#00D8FF' dataPoints={expense_record} date_array={date_array} label="Expense" />

    </>
  )
}

export default Dashboard
